.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.inputSet {
  display: flex;
  align-items: center;
  gap: 10px;
}
.input-container {
  width: 300px;
  height: 30px;
  border-radius: 10px;
  border: 1px solid lightgray;
  box-shadow: 1px 1px 1px 1px gray;
  padding-left: 10px;
}
.btn {
  background-color: gray;
  color: whitesmoke;
  border: none;
  border-radius: 1em;
  width: 60px;
  height: 35px;
  box-shadow: 1px 1px 1px 1px rgb(93, 93, 93);
}
.list-container {
  width: 500px;
  height: 500px;
  border: 1px solid gray;
  border-radius: 1em;
  margin-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  box-shadow: 1px 1px 1px 1px lightgray;
}
.checkList {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.checkBtn {
  height: 35px;
  width: 35px;
  background-color: white;
  cursor: pointer;
}
